@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Nunito', sans-serif;

}



.App {
  text-align: center;
}

header {
    background: -webkit-gradient(linear,left top, left bottom,from(#0b3e71),to(rgba(11,65,118,.5)));
    background: linear-gradient(180deg,#0b3e71,rgba(11,65,118,.5));
    background-color: rgba(11,65,118,.8);
    background-repeat: repeat-x;
    color: white;
    font-size: 32px;
    padding: 10px;
}

header h3 {
    margin: 0;
}
.question p {
    font-family: 'Nunito', sans-serif;
    font-size: 72px;
}
.buttons {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.buttons button {
    font-family: 'Nunito', sans-serif;
    font-size: 19px;
    padding: 12px 18px;
    border-radius: 16px;
    border-style: solid;
    border-width: 2px 2px 4px;
    background: none;
    color: #4b4b4b;
    cursor: pointer;
    font-weight: 500;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-transition: -webkit-filter .2s;
    transition: -webkit-filter .2s;
    transition: filter .2s;
    transition: filter .2s, -webkit-filter .2s;
    line-height: 30px;
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
    border-color: #e5e5e5;
}

.buttons button:focus {
    outline: 0;
}

.buttons button:active {
    border-width: 2px 2px 2px;
}

.buttons button:hover {
    background: #ecebeb;
}
