.buttons {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.buttons button {
    font-family: 'Nunito', sans-serif;
    font-size: 19px;
    padding: 12px 18px;
    border-radius: 16px;
    border-style: solid;
    border-width: 2px 2px 4px;
    background: none;
    color: #4b4b4b;
    cursor: pointer;
    font-weight: 500;
    transform: translateZ(0);
    transition: filter .2s;
    line-height: 30px;
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
    border-color: #e5e5e5;
}

.buttons button:focus {
    outline: 0;
}

.buttons button:active {
    border-width: 2px 2px 2px;
}

.buttons button:hover {
    background: #ecebeb;
}